.container {
    max-width: 97%;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.link {
    text-decoration: none;
    padding: 10px;
    color: #fff;
}

.active {
    color: #ffffff33;
}

.toggleButton {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
}